import Vue from 'vue'
import VueRouter from 'vue-router'



Vue.use(VueRouter)

const routes = [
  {
    path: '/*',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/Login.vue')
  },
  {
    path: '/proveedor',
    name: 'GestionProveedor',
    component: () => import(/* webpackChunkName: "about" */ '../views/GestionProveedor.vue')
  },

  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/Login.vue')
  },

  {
    path: '/producto',
    name: 'producto',
    component: () => import(/* webpackChunkName: "about" */ '../views/GestionProducto.vue')
  },
  {
    path: '/venta',
    name: 'venta',
    meta: {
      requiresAuth: true
    },
    component: () => import(/* webpackChunkName: "about" */ '../views/Venta.vue')
  },
  {
    path: '/critico',
    name: 'critico',
    component: () => import(/* webpackChunkName: "about" */ '../views/Critico.vue')
  },
  {
    path: '/balance',
    name: 'balance',
    component: () => import(/* webpackChunkName: "about" */ '../views/Balance.vue')
  },

  {
    path: '/devolucion',
    name: 'devolucion',
    component: () => import(/* webpackChunkName: "about" */ '../views/Devolucion.vue')
  }
  

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
