import Vue from 'vue';
import Vuetify from 'vuetify';
import 'vuetify/dist/vuetify.min.css';

Vue.use(Vuetify);



export default new Vuetify({
  theme: {
      options: {
        customProperties: true,
      },
    themes: {
      light: {
        primary: '#FB8C00',
        secondary: '#010440',
        accent: '#82B1FF',
        error: '#FF5252',
        info: '#1565C0',
        success: '#4CAF50',
        warning: '#FFC107'
      },
    },
  },
});
