<template>
  <v-layout>
    <v-toolbar
      dark
      dense
      prominent
      src="../assets/banner.webp"
    >
    
      <v-flex>
        <v-col cols="12">
          <h1 color="#EBD84D">Ferreteria Cato</h1>
        </v-col>
      </v-flex>

      <v-layout wrap class="mt-10">
        
        <v-flex class="ml-3">
          <v-btn color="#FFFFFF" :to="{ name: 'venta' }">
            <h3 style="color:black;">venta</h3>
            <v-icon class="ml-1" color="#000000">local_grocery_store</v-icon>
          </v-btn>
        </v-flex>

        <!-- Botones desplegables de transaccion, producto y proveedor -->
        <v-flex>
          <div class="text-center">
            <v-menu offset-y>
              <template v-slot:activator="{ on }">
                <v-btn color="#FFFFFF" dark v-on="on">
                <h3 style="color:black;">Gestion</h3>
                <v-icon class="ml-1" color="#000000">border_color</v-icon>
                </v-btn>
              </template>
              <v-list>
                <v-list-item to="/devolucion">
                  <v-list-item>Devolución de producto</v-list-item>
                </v-list-item>

                <v-list-item to="/balance">
                  <v-list-item>Balance</v-list-item>
                </v-list-item>
              </v-list>
            </v-menu>
          </div>
        </v-flex>
        <!-------------------------------------------------------------------->
        <v-flex>
          <div class="text-center">
            <v-menu offset-y>
              <template v-slot:activator="{ on }">
                <v-btn :color="allCritics.length>0? '#FF3C00':'#FFFFFF'" dark v-on="on">
                <h3 style="color:black;">Productos</h3>
                  <v-icon class="ml-1" color="#000000">menu_book</v-icon>
                </v-btn>
              </template>
              <v-list>
                <v-list-item to="/producto">
                  <v-list-item>Gestion</v-list-item>
                </v-list-item>

                <v-list-item to="/critico">
                  <v-list-item>Stock Critico</v-list-item>
                </v-list-item>

              </v-list>
            </v-menu>
          </div>
        </v-flex>
        <!-------------------------------------------------------------------->

        <v-flex>
          <div class="text-center">
            <v-menu offset-y>
              <template v-slot:activator="{ on }">
                <v-btn color="#FFFFFF" dark v-on="on" to="/proveedor">
                <h3 style="color:black;">Proveedores</h3>
                  <v-icon class="ml-1" color="#000000 ">local_shipping</v-icon>
                </v-btn>
              </template>

            </v-menu>
          </div>
        </v-flex>
        <!-- Fin de los botones -->
      </v-layout>
    </v-toolbar>
  </v-layout>
</template>

<script>
import axios from "axios";
export default {
  data() {
    
    
    return {
      allCritics:[]
      };
  },
    mounted: function() {

    this.getAllCritics();

  },
  methods: {
        getAllCritics() {
      axios
        .get("http://127.0.0.1:8000/api/critical/")
        .then(response => (this.allCritics = response.data));
    }
  }
};
</script>
